import React, { createContext } from 'react'
import Header from './Header'
import Footer from './Footer'
import styles from './styles/Home.module.css'
import { BrowserRouter, Link, Route, Routes } from 'react-router-dom'
import F_calc from './F/F_calc'
import Max_calc from './Max/Max_calc'
import { ShowUser } from './UserManage'

function Home() {
  return (
    <div className={styles.Home}>
      <Header></Header>
      <main className={styles.main}>
        <div className={styles.title}>
          <h1><span className={styles.blank_space}></span></h1>
          <h1></h1>
        </div>
        <div className={styles.news}>
          <h3></h3>
          <p></p>
        </div>
        <div className={styles.SiteMenu}>
          <ul className={styles.CardList}>
            <Link to={'/Table'} className={styles.HomeLinkElement}>E</Link>
            <Link to={'/F'} className={styles.HomeLinkElement}>F</Link>
            <Link to={'/Max'} className={styles.HomeLinkElement}>Max</Link>
            <Link to={'/Create'} className={styles.HomeLinkElement}>E</Link>
            <Link to={'/Other'} className={styles.HomeLinkElement}></Link>
          </ul>
        </div>
      </main>
      <Footer></Footer>
    </div>
  )
}

export default Home